.app-logo{height:40vmin;pointer-events:none;margin:0 auto}.app-header{display:flex;flex-direction:row;width:1024px;margin:0 auto;padding:10px}@media (prefers-reduced-motion: no-preference){.app-logo{-webkit-animation:app-logo-spin infinite 20s linear;animation:app-logo-spin infinite 20s linear}}@-webkit-keyframes app-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes app-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

